import {List, Button, Spin,Icon,Rate,Tag,message,Modal ,Input} from 'antd';
import React from 'react';
import {Link} from 'react-router-dom';


const data = [
    {
        "title": "写在前面的话",
        "description": "",
        "url": "/dp/font",
        "typeColor": "cyan",
        "typeName": "dp",
        "updateTime": "2019-02-14"
    },{
        "title": "墨菲定律",
        "description": "",
        "url": "/dp/Murphy'sLaw",
        "typeColor": "cyan",
        "typeName": "dp",
        "updateTime": "2019-02-14"
    },{
        "title": "微服务架构实践之服务容错",
        "description": "",
        "url": "/dp/dp4Failure",
        "typeColor": "cyan",
        "typeName": "dp",
        "updateTime": "2019-06-14"
    } ,{
        "title": "防御性编程",
        "description": "",
        "url": "/dp/DefinsivePrograming",
        "typeColor": "cyan",
        "typeName": "dp",
        "updateTime": "2019-06-15"
    }

]//DefinsivePrograming





class Frames extends React.Component {
    constructor(props) {
        super(props);
        if(!window.erred) {
            window.erred = 0;
        }else if(window.erred && window.erred > 2 && window.erred  <= 3){
            message.error('你已经受限访问');
            window.erred = window.erred +1 ;
        }else if(window.erred && window.erred > 3 ){
            window.location.replace("http://news.baidu.com/")
        }
        /*console.log(props);*/
        this.state = {
            loading: true,
            loadingMore: false,
            showLoadingMore: true,
            remaining :0,
            key:'',
            visible: false,
            total :0,
            data: [],
        }
        /*console.log(props);*/
        const  q = props.location.search ;
        if(q){
            if(q =='?2019'){
                window.needInput = 1 ;
            }
        }
    }

    pigeNum = 1;

    componentDidMount() {
        this.getData((results) => {
            this.setState({
                loading: false,
                data: results,
            });
        });
    }

    async getData(callback) {
        const pageSize = 15;
        let arr = [];
        let lastIndex = this.pigeNum * pageSize;

        if (this.pigeNum * pageSize >= data.length) {
            lastIndex = data.length;
        }
        this.remaining = data.length - lastIndex ;
        this.setState({
            remaining: data.length - lastIndex ,
            total:data.length,
        });

        for (let j = (this.pigeNum - 1) * pageSize; j < lastIndex; j += 1) {
            arr.push(data[j])
        }

        callback(arr)
        this.pigeNum = this.pigeNum + 1

    }


    onLoadMore = () => {
        if(!window.needInput || window.needInput != 1){
            this.setState({
                visible: true,
            });
            return ;
        }
        this.setState({
            loadingMore: true,
        });
        this.getData((results) => {
            const data = this.state.data.concat(results);
            this.setState({
                data,
                loadingMore: false,
            }, () => {
                window.dispatchEvent(new Event('resize'));
            });
        });
    }
    handleOk = (e) => {
        this.setState({
            visible: false,
        });
        if(this.state.key && ( this.state.key == '2019')){
            window.needInput = 1 ;
            this.onLoadMore() ;
        }
    }
    handleCancel = (e) => {
        this.setState({
            visible: false,
        });
    }
    emitEmpty = () => {
        this.keyInput.focus();
        this.setState({ key: '' });
    }
    onChangeUserName = (e) => {
        this.setState({ key: e.target.value });
    }

    render() {
        const IconText = ({ type, text }) => (
            <span> <Icon type={type} style={{ marginRight: 8 }} />
                {text}
            </span>
        );
        const suffix = key ? <Icon type="close-circle" onClick={this.emitEmpty} /> : null;
        const {loading, loadingMore, showLoadingMore, data,remaining,total,key} = this.state;
        const loadMore = showLoadingMore ? (
            <div style={{textAlign: 'center', marginTop: 12, height: 32, lineHeight: '32px'}}>
                {loadingMore && <Spin/>}
                {!loadingMore && <Button onClick={this.onLoadMore}>  loading more, total {total} remaining {remaining}  </Button>}
            </div>
        ) : null;
        return (
            <div>
                <Modal
                    title="添加微信好友,请注明github"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <img src={require('../../../imgs/wx.png' )} style={{height:"100%",width:"100%",marginBottom:"10px"}}/>
                    <Input
                        placeholder="请输入图中的微信号或验证码"
                        prefix={<Icon type="question-circle-o" style={{ color: 'rgba(0,0,0,.25)' }} />}
                        suffix={suffix}
                        value={key}
                        onChange={this.onChangeUserName}
                        ref={node => this.keyInput = node}
                    />

                </Modal>
                <List
                    className="demo-loadmore-list"
                    loading={loading}
                    itemLayout="horizontal"
                    loadMore={loadMore}
                    dataSource={data}
                    renderItem={item => (
                        <List.Item >
                            <List.Item.Meta
                                title={
                                    <span>

                                 <Link to={item.url}> <span className="link"> {item.title}</span></Link>
                                </span>
                                }
                                description={
                                    <span>{item.description }<p></p><Tag>{item.typeName}</Tag><IconText type="calendar" text={item.updateTime} />
                                <br/></span>
                                }
                            />
                        </List.Item>

                    )}
                />
            </div>
        );
    }
}

export default Frames;


